import Axios from "axios"

const rad = (ang) => ang / 360 * (2 * Math.PI)
//const ang = (rad) => rad / (2 * Math.PI) * 360

export const computeDist = ( coordsA, coordsB) => {
    const la1 = rad(coordsA[0])
    const lo1 = rad(coordsA[1])
    const la2 = rad(coordsB[0])
    const lo2 = rad(coordsB[1])
    return Math.acos( Math.sin(la1) * Math.sin(la2) + Math.cos(la1) * Math.cos(la2) * Math.cos(lo2-lo1)  )
}

export const computeP = ( answers ) => {
    let somatorio = 0
    answers.forEach(e => {
        somatorio += e.distancia
    });

    return Axios.post(
        'https://apps.idor.org/faas/function/rv-pvalue', 
        somatorio,
        { headers: { 'Content-Type': 'text/plain;charset=UTF-8' } }
    )
}


